import React, { useContext, useState } from "react";

import Card from "../../shared/components/UIElements/Card";
import Input from "../../shared/components/FormElements/Input";
import { useForm } from "../../shared/hooks/form-hook";

import "./Auth.css";
import {
  VALIDATOR_EMAIL,
  VALIDATOR_MINLENGTH,
  VALIDATOR_REQUIRE,
} from "../../shared/util/validators";
import Button from "../../shared/components/FormElements/Button";
import { AuthContext } from "../../shared/content/context-hook";

const Auth = () => {
  const [isLogin, setIsLogin] = useState(true);

  const auth = useContext(AuthContext);

  const [formState, inputHandler,setFormData] = useForm(
    {
      email: {
        value: "",
        isValid: false,
      },
      password: {
        value: "",
        isValid: false,
      },
    },
    false
  );

  const submitHandler = (event) => {
    event.preventDefault();
    console.log(formState.inputs);
    auth.login();
  };

  const loginSwitchHandlder = ()=>{
    if(!isLogin){
        setFormData({
            ...formState.inputs,
            name:undefined
        },formState.inputs.password.isValid && formState.inputs.email.isValid)
    }else{
        setFormData({
            ...formState.inputs,
            name:{
                value:'',
                isValid:false
            }
        },false)
    }
    setIsLogin(prevMode => !prevMode)
  }

  return (
    <Card className="authentication">
      <h2>Sign in required!</h2>
      <hr />
      <form onSubmit={submitHandler}>
        {!isLogin && (
          <Input
            id="name"
            element="input"
            type="text"
            label="Name"
            errorText="Please enter your name!"
            validators={[VALIDATOR_REQUIRE()]}
            onInput={inputHandler}
          />
        )}
        <Input
          id="email"
          element="input"
          type="email"
          label="E-mail"
          errorText="Please provide a valid email."
          validators={[VALIDATOR_EMAIL()]}
          onInput={inputHandler}
        />
        <Input
          id="password"
          element="input"
          type="password"
          label="Password"
          errorText="Please provide a safe password."
          validators={[VALIDATOR_MINLENGTH(8)]}
          onInput={inputHandler}
        />

        <Button type="submit" disabled={!formState.isValid}>
          {isLogin ? "Log in":"Sign up"}
        </Button>
      </form>
      <Button onClick={loginSwitchHandlder} inverse>Switch to {isLogin ? "Sign up":"Log in"}</Button>
    </Card>
  );
};

export default Auth;
