import React, { useContext } from "react";
import { NavLink } from "react-router-dom";
import { AuthContext } from "../../content/context-hook";

import "./NavLinks.css";

const NavLinks = () => {
  const Auth = useContext(AuthContext);

  return (
    <ul className="nav-links">
      <li>
        <NavLink to="/" exact>
          ALL USERS
        </NavLink>
      </li>
      <li>{Auth.isLoggedIn && <NavLink to="/u1/places">MY PLACES</NavLink>}</li>
      <li>
        {Auth.isLoggedIn && <NavLink to="/places/new">NEW PLACE</NavLink>}
      </li>
      <li>{!Auth.isLoggedIn && <NavLink to="/auth">AUTHENTICATE</NavLink>}</li>
      <li>
        {Auth.isLoggedIn && <button onClick={Auth.logout}>SIGN OUT</button>}
      </li>
    </ul>
  );
};

export default NavLinks;
