import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import Button from "../../shared/components/FormElements/Button";

import Input from "../../shared/components/FormElements/Input";
import Card from "../../shared/components/UIElements/Card";
import { useForm } from "../../shared/hooks/form-hook";
import { VALIDATOR_REQUIRE } from "../../shared/util/validators";

import "./NewPlace.css";

const DUMMY_PLACES = [
  {
    id: "p1",
    title: "Downtown Vancouver",
    address: "555 W Hastings St, Vancouver, BC V6B 4N6, Canada",
    description: `Give this a go. Fantastic views all around the city and beyond and the lift ride up to the top is fun too.
      Thanks to the staff for looking after us today too in our "@" hour of need. You'll know what I mean ðŸ˜Š`,
    imageUrl:
      "https://images.dailyhive.com/20200103202317/vancouver-house-tower.jpg",
    coordinates: {
      lat: 49.2847323,
      lng: -123.1141666,
    },
    creator: "u1",
  },
  {
    id: "p2",
    title: "Downtown NYC",
    address: "United States, New York",
    description: `New York City comprises 5 boroughs sitting where the Hudson River meets the Atlantic Ocean. At its core is Manhattan, 
      a densely populated borough thatâ€™s among the worldâ€™s major commercial, financial and cultural centers.`,
    imageUrl:
      "https://i.natgeofe.com/k/5b396b5e-59e7-43a6-9448-708125549aa1/new-york-statue-of-liberty_16x9.jpg",
    coordinates: {
      lat: 40.6974881,
      lng: -73.979681,
    },
    creator: "u2",
  },
  {
    id: "p3",
    title: "Downtown NYC",
    address: "United States, New York",
    description: `New York City comprises 5 boroughs sitting where the Hudson River meets the Atlantic Ocean. At its core is Manhattan, 
      a densely populated borough thatâ€™s among the worldâ€™s major commercial, financial and cultural centers.`,
    imageUrl:
      "https://i.natgeofe.com/k/5b396b5e-59e7-43a6-9448-708125549aa1/new-york-statue-of-liberty_16x9.jpg",
    coordinates: {
      lat: 40.6974881,
      lng: -73.979681,
    },
    creator: "u1",
  },
];

const UpdatePlace = () => {
  const placeId = useParams().placeId;
  const [isLoading, setIsLoading] = useState(true);
  const [formState, inputHandler, setFormData] = useForm(
    {
      title: {
        value: "",
        isValid: false,
      },
      description: {
        value: "",
        isValid: false,
      },
    },
    true
  );

  const place = DUMMY_PLACES.find((p) => p.id === placeId);

  useEffect(() => {
    if (place) {
      setFormData(
        {
          title: {
            value: place.title,
            isValid: true,
          },
          description: {
            value: place.description,
            isValid: true,
          },
        },
        true
      );
    }

    setIsLoading(false);
  }, [setFormData, place]);

  if (!place) {
    return (
      <div className="center">
        <Card> Sorry! We couldn't find the place.</Card>
      </div>
    );
  }

  if (isLoading) {
    return <Card className="center">loading...</Card>;
  }

  const placeUpdateSubmitHandler = (event) => {
    event.preventDefault();
    console.log(formState.inputs);
  };

  return (
    <form className="place-form" onSubmit={placeUpdateSubmitHandler}>
      <Input
        id="title"
        element="input"
        type="text"
        onInput={inputHandler}
        label="Title"
        validators={[VALIDATOR_REQUIRE()]}
        errorText="Please enter a valid title!"
        value={formState.inputs.title.value}
        isValid={formState.inputs.title.isValid}
      />
      <Input
        id="description"
        element="input"
        type="text"
        onInput={inputHandler}
        label="Description"
        validators={[VALIDATOR_REQUIRE()]}
        errorText="Please enter a valid description(minimum is 5 characters)!"
        value={formState.inputs.description.value}
        isValid={formState.inputs.description.isValid}
      />
      <Button type="submit" disabled={!formState.isValid}>
        Update Place
      </Button>
    </form>
  );
};

export default UpdatePlace;
